.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-link {
  color: #fee6e3;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1{
  margin-top: 3%;
  margin-left: 20%;
  margin-right: 20%;
}

table{
  align-self: center;
}
table td{
  padding: 0px 20px;
}
p{
  text-align: center;
  padding-bottom: 30px;
  font-size: 18px;
}
#explain{
  margin-bottom: 20px;
  margin-top: 40px;
  margin-left: 25%;
  margin-right: 25%;
  font-size: 16px;
}

.square {
  height: 350px;
  margin: auto;
  display: flex;
  justify-content:center;
  width: 90%;
  position: relative;
  align-items: center;
}
.vertical-center{
  position: relative;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  align-items: center;
}

.song{
  margin: auto;
  font-size: 20px;
  margin-left: 35%;
  margin-right: 35%;
  text-align:center;
}
img{
  width: 80px;
  height: 80px;
  cursor:pointer;
}
a{
  color: #ce867c;
}
